@media only screen and (min-width: 320px) {
  .gallery {
    position: relative;
    min-height: 200px;
    padding: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    ul {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      margin-bottom: 0;
      padding-left: 0;
      list-style-type: none;
      li {
        float: left;
        width: 100%;
      }
      a {
        display: block;
        height: 200px;
      }
      img {
        height: 100%;
        width: 100%;
      }
    }
    ol {
      position: absolute;
      bottom: 0;
      right: 1%;
      margin-bottom: 0;
      padding-left: 0;
      li {
        cursor: pointer;
        display: inline-block;
        width: 9px;
        height: 9px;
        background-color: #FFF;
        @include border-radius(50%);
        &.active {
          background-color: #999;
        }
      }
    }
  }
  .photo-nav {
    width: 10%;
    top: 50%;
    position: absolute;
    text-align: center;
    font-size: 20px;
    color: #999;
    @include transform(translateY(-50%));
    &:hover,
    &:active,
    &:visited {
      color: #FFF;
    }
    &.next-nav {
      left: 90%;
    }
    &.previous-nav {
      left: 0;
    }
  }
}

@media only screen and (min-width: 640px) {}

@media only screen and (min-width: 768px) {
  .gallery {
    min-height: 300px;
    ul a {
      height: 300px;
    }
  }
}

@media only screen and (min-width: 992px) {}

@media only screen and (min-width: 1200px) {}
